import React, { useEffect } from "react";
import axios from "axios";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { actionCreators } from "../store/index";

const HomePage = () => {
  const dispatch = useDispatch();
  const setLogin = useSelector((state) => state.setLogin);
  const navigate = useNavigate();
  const [error, setError] = useState("");

  useEffect(() => {
    axios
      .get("https://b2b-order-invoice.onrender.com//api/orders/get-items")
      .then((res) => {
        dispatch(actionCreators.initialiseProducts(res.data));
        dispatch(actionCreators.initialiseCart({}));
      });
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    let sum = 0;
    let index = 0;
    for (; index < cart.cart.length; index += 1) {
      sum += cart.cart[index].MRP * cart.cart[index].qty;
    }
    setPrice(sum);
  });

  const checkLogin = () => {
    if (setLogin.accessToken === "") {
      navigate("/login");
    }
  };

  const cart = useSelector((state) => state.cart);
  const [price, setPrice] = useState(0);
  // console.log(cart);

  const handleChangeTextField = (name, value) => {
    checkLogin();
    let tempvalue = Number(value);
    dispatch(actionCreators.updateCart(name, tempvalue));
    calculatePrice();
  };

  const calculatePrice = () => {
    let sum = 0;
    let index = 0;
    for (; index < cart.cart.length; index += 1) {
      sum += cart.cart[index].MRP * cart.cart[index].qty;
    }
    setPrice(sum);
  };

  const handleCheckout = () => {
    let finalCart = [];
    let quanZero = 0;
    for (let i = 0; i < cart.cart.length; i += 1) {
      if (cart.cart[i].qty === 0) quanZero += 1;
      let tempobj = {
        itemId: cart.cart[i]._id,
        quantity: cart.cart[i].qty,
      };
      finalCart.push(tempobj);
    }
    if (quanZero === cart.cart.length) return;

    axios
      .post(
        "https://b2b-order-invoice.onrender.com//api/orders/make-order",
        {
          itemCount: finalCart,
        },
        {
          headers: {
            "Content-type": "Application/json",
            Authorization: "Bearer " + setLogin.accessToken,
          },
        }
      )
      .then((res) => {
        console.log(res);
        if (res.status === 200) {
          console.log("success");
          dispatch(actionCreators.initialiseCart());
          navigate("/previousOrders");
        } else {
          console.log("not success");
        }
      })
      .catch((err) => {
        setError("Error: Server Error");
      });
  };

  return (
    <>
      <section className="h-100 gradient-custom">
        {error && <p className="error">{error}</p>}
        <div className="container py-5">
          <div className="row d-flex justify-content-center my-4">
            <div className="col-md-8">
              <div className="card mb-4">
                {cart.cart.map((element) => {
                  return (
                    <div className="card-body">
                      <div className="row">
                        <div className="col-lg-4 col-md-12 mb-4 mb-lg-0">
                          <p>
                            <strong> DSIN : {element.DSIN} </strong>
                          </p>

                          <p>
                            <strong> HSN_CODE : {element.HSN_CODE} </strong>
                          </p>

                          <p>
                            <strong> GST : {element.GST} </strong>
                          </p>
                        </div>

                        <div className="col-lg-5 col-md-6 mb-4 mb-lg-0">
                          <p>
                            <strong>{element.NAME}</strong>
                          </p>
                          <button
                            type="button"
                            className="btn btn-primary btn-sm me-1 mb-2"
                            data-mdb-toggle="tooltip"
                            title="Remove item"
                            onClick={() => {
                              checkLogin();
                              dispatch(
                                actionCreators.updateCart(element.NAME, 0)
                              );
                            }}
                          >
                            <i className="fas fa-trash"></i>
                          </button>
                        </div>

                        <div className="col-lg-3 col-md-6 mb-4 mb-lg-0">
                          <div
                            className="d-flex mb-4"
                            style={{ maxWidth: "300px" }}
                          >
                            <button
                              className="btn btn-primary px-3 me-2"
                              onClick={() => {
                                checkLogin();
                                if (element.qty <= 0) return;
                                dispatch(
                                  actionCreators.removeFromCart(element.NAME)
                                );
                              }}
                            >
                              <i className="fas fa-minus"></i>
                            </button>

                            <div className="form-outline">
                              <input
                                id="form1"
                                min="0"
                                name="quantity"
                                value={element.qty}
                                type="number"
                                className="form-control"
                                onChange={(e) => {
                                  handleChangeTextField(
                                    element.NAME,
                                    e.target.value
                                  );
                                }}
                              />
                            </div>

                            <button
                              className="btn btn-primary px-3 ms-2"
                              onClick={() => {
                                checkLogin();
                                dispatch(
                                  actionCreators.addToCart(element.NAME)
                                );
                              }}
                            >
                              <i className="fas fa-plus"></i>
                            </button>
                          </div>

                          <p className="text-start text-md-center">
                            <strong>Rs{element.MRP}</strong>
                          </p>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
            <div className="col-md-4">
              <div className="card mb-4">
                <div className="card-header py-3">
                  <h5 className="mb-0">Summary</h5>
                </div>
                <div className="card-body">
                  <ul className="list-group list-group-flush">
                    <li className="list-group-item d-flex justify-content-between align-items-center border-0 px-0 pb-0">
                      Products
                      <span>Rs{price}</span>
                    </li>
                    <li className="list-group-item d-flex justify-content-between align-items-center border-0 px-0 mb-3">
                      <div>
                        <strong>Total amount</strong>
                        <strong>
                          <p className="mb-0">(including VAT)</p>
                        </strong>
                      </div>
                      <span>
                        <strong>Rs{price}</strong>
                      </span>
                    </li>
                  </ul>

                  <button
                    className="btn btn-primary btn-lg btn-block"
                    onClick={handleCheckout}
                  >
                    Checkout
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default HomePage;
