const initial_state = {
    cart : [],
    products : []
}

const reducer =(state=initial_state , action)=>{
    if(action.type === "addToCart"){
        state.cart.find((element)=>{
            if(element.NAME === action.payload){
                // console.log(typeof(element.qty));
                element.qty = element.qty+1;
            }
        })
        // console.log(state.cart);
        return{
            ...state
        }
    }
    else if(action.type === "removeFromCart"){
        state.cart.find((element)=>{
            if(element.NAME === action.payload){
                element.qty = element.qty-1;
            }
        })
        return{
            ...state
        }
    }
    else if(action.type === "initialiseCart"){
        let emptyCart = [];
        for(let i=0 ; i<state.products.length ; i += 1){
            const cur = {
                ...state.products[i],
                qty : 0,
            }
            emptyCart.push(cur);
        }
        // console.log(typeof(emptyCart[0].qty));
        return {
            ...state,
            cart : emptyCart
        };
    }
    else if(action.type === "initialiseProducts"){
        return {
            ...state,
            products : action.payload
        };
    }
    else if(action.type === "updateCart"){
        // console.log(typeof(action.payload.qty));
        state.cart.find((element)=>{
            if(element.NAME === action.payload.name){
                element.qty = action.payload.qty;
            }
        })
        // console.log(state.cart);
        return{
            ...state
        }
    }
    else return state;
}

export default reducer;