import { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { useDispatch } from "react-redux";
import { actionCreators } from "../store/index";

const LoginPage = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const logIn = async () => {
    try {
      const { data } = await axios.post(
        "https://b2b-order-invoice.onrender.com//api/auth/login",
        {
          email: username,
          password: password,
        },
        {
          headers: {
            "content-type": "application/json",
          },
        }
      );
      const obj = {
        accessToken: data.accessToken,
      };
      dispatch(actionCreators.setToken(obj));
      dispatch(actionCreators.initialiseCart(obj));
      navigate("/");
    } catch (err) {
      setError(err.response.data.error.message);
    }
  };

  return (
    <>
      <h1>Log In</h1>
      {error && <p className="error">{error}</p>}
      <input
        value={username}
        placeholder="Your username addrress"
        onChange={(e) => setUsername(e.target.value)}
      />
      <input
        type="password"
        value={password}
        placeholder="Your password"
        onChange={(e) => setPassword(e.target.value)}
      />
      <button onClick={logIn}>Log In</button>
      <Link to="/create-account" className="account-text">
        Don't have an account? Create one here.
      </Link>
    </>
  );
};

export default LoginPage;
