import React, { useEffect, useState } from "react";
import axios from "axios";
import { useSelector } from "react-redux";
import { saveAs } from "file-saver";

export default function Request() {
  const setLogin = useSelector((state) => state.setLogin);
  const [request, setRequest] = useState([]);
  const [error, setError] = useState("");
  const getOrders = async () => {
    try {
      const res = await axios.get(
        "https://b2b-order-invoice.onrender.com//api/orders/user-orders",
        {
          headers: {
            "Content-type": "Application/json",
            Authorization: "Bearer " + setLogin.accessToken,
          },
        }
      );
      console.log(res);
      let tempRequest = res.data;
      for (let i = 0; i < res.data.length; i += 1) {
        let temparr = [];
        for (let j = 0; j < res.data[i].itemCount.length; j += 1) {
          if (res.data[i].itemCount[j].quantity != 0) {
            temparr.push(res.data[i].itemCount[j]);
          }
        }
        tempRequest[i].itemCount = temparr;
      }
      tempRequest.reverse();
      setRequest(tempRequest);
    } catch (err) {
      setError("Error: Server Error");
    }
  };

  useEffect(() => {
    getOrders();
  }, []);

  const getInvoice = (orderId, itemCount) => {
    const axiosConfig = {
      responseType: "blob",
      headers: {
        Accept: "application/json",
        Authorization: "Bearer " + setLogin.accessToken,
      },
    };

    axios
      .post(
        "https://b2b-order-invoice.onrender.com//api/orders/invoice",
        { _id: orderId, itemCount },
        axiosConfig
      )
      .then((res) => {
        const url = window.URL.createObjectURL(
          new Blob([res.data], { type: "application/pdf" })
        );
        var link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "invoice.pdf");
        document.body.appendChild(link);
        link.click();
      });
    // axios.post("https://b2b-order-invoice.onrender.com//api/orders/invoice" ,
    // {
    //     _id : orderId ,
    //     itemCount : itemCount
    // },
    // {
    //     headers : {
    //         'Authorization': 'Bearer ' + setLogin.accessToken,
    //         'responseType': 'arraybuffer'
    //     }
    // })
    // .then((res) =>{
    //     console.log(res)
    //     const pdfBlob = new Blob([res.data] , {type : 'application/pdf'});
    //     saveAs(pdfBlob , 'invoice.pdf');
    // })

    // .then(res => {
    //   const url = window.URL.createObjectURL(new Blob([res.data]
    //     ,{type: "application/pdf"}))
    //   var link = document.createElement('a');
    //   link.href = url;
    //   link.setAttribute('download', 'invoice.pdf');
    //   document.body.appendChild(link);
    //   link.click();
    // })
  };

  return (
    <div>
      <div>
        <section className="h-100 gradient-custom">
          <div className="container py-5">
            <div className="row d-flex justify-content-center my-4">
              <div className="col-md-8">
                <div className="card mb-4">
                  <div className="card-header py-3">
                    <h5 className="mb-0">Previous Orders</h5>
                  </div>
                  {request.map((element) => (
                    <>
                      <div className="row">
                        <div className="col mb-lg-0">
                          <p className="my-auto">
                            <strong>Order Id:{element._id}</strong>
                          </p>
                        </div>
                        <div className="col mb-lg-0">
                          {element.itemCount.map((element1) => {
                            return (
                              <div className="card-body">
                                <div className="row">
                                  <div className="col  mb-lg-0">
                                    <p>
                                      <strong>{element1.itemId.NAME}</strong>
                                    </p>
                                  </div>

                                  <div className="col  mb-lg-0">
                                    <p id="form1" type="number">
                                      {element1.quantity}
                                    </p>
                                  </div>
                                </div>
                              </div>
                            );
                          })}
                        </div>
                        <hr />
                      </div>

                      <button
                        onClick={() =>
                          getInvoice(element._id, element.itemCount)
                        }
                      >
                        get invoice
                      </button>
                    </>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
}
