import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useUpdateUsername, useUsername } from "../hook/useUsername";
import { useDispatch } from "react-redux";
import {actionCreators} from '../store/index'

const NavBar = () => {
  const dispatch = useDispatch();
  const setLogin = useSelector(state=>state.setLogin);
  const accessToken = setLogin.accessToken;

  const handleLogout = ()=>{
    dispatch(actionCreators.logOutSession());
  }

  return (
    <nav>
      <ul>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          {!accessToken ? (
            <Link to="/login">Login</Link>
          ) : (
            <>
            <Link to="/" onClick={handleLogout}>
              Sign Out
            </Link>
            <Link to="/previousOrders">
              Previous Orders
            </Link>
            </>
          )}
        </li>
      </ul>
    </nav>
  );
};

export default NavBar;
